<template>
  <div class="index" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="...">
    <div>
    <input type="file" @change="importFile(this)" id="imFile" style="display: none"
           accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"/>
    <a id="downlink"></a>
    <el-button class="button" @click="uploadFile('train')"></el-button>
    <el-button class="button" @click="uploadFile('test')"></el-button>
    <el-button class="button" @click="uploadFile('jpg')"></el-button>
    <el-button class="button" @click="run()"></el-button>
    </div>
    <!---->
    <el-dialog title="" v-model="errorDialog" size="tiny">
      <span>{{errorMsg}}</span>
      <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="errorDialog=false"></el-button>
  </span>
    </el-dialog>
    <div style="float: left;  width: 40%">
      <el-card class="box-card">
        <div  class="text item">
          :{{ trainingData.length>0?' ':' ' }}<el-button type="primary" @click="showTrain()"></el-button>
        </div>
        <div  class="text item">
          :{{ testData.length>0?' ':' ' }}<el-button type="primary" @click=""></el-button>
        </div>
        <div  class="text item">
          &nbsp;&nbsp;&nbsp;&nbsp;:{{ loggingImage>0?' ':' ' }}
        </div>
        <div  class="text item">
          &nbsp;&nbsp;&nbsp;&nbsp;:
          <el-button class="button" v-if="resultShow" @click="downloadFile(testData)"></el-button>
        </div>
      </el-card>
    </div>
    <div style="float: right; width: 60%">
      <h1>vue-xlsx-table</h1>
      <vue-xlsx-table @on-click-ok="showTrain"></vue-xlsx-table>
    </div>

  </div>
</template>

<script>
  import Vue from 'vue'
  // xlsx
  var XLSX = require('xlsx')
  export default {
    name: 'Index',
    data () {
      return {
        tableData: [
          {"name":"","tel":"156*****1987","hobby":"","address":"56917"},
          {"name":"","tel":"182*****1538","hobby":"","address":"122"},
          {"name":"","tel":"161*****0097","hobby":"","address":"739"},
          {"name":"","tel":"197*****1123","hobby":"","address":"24"},
          {"name":"","tel":"183*****6678","hobby":"","address":"867-871"}
        ],
        columns: [
          {field: 'name', title:'name'},
          {field: 'tel', title: 'tel'},
          {field: 'hobby', title: 'hobby'},
          {field: 'address', title: 'address'}
        ],
        resultShow: false,
        result: [],
        trainingData: [],
        testData: [],
        loggingImage: '',
        fullscreenLoading: false, // 
        imFile: '', // el
        outFile: '',  // el
        errorDialog: false, // 
        errorMsg: '', // 
        butt: ''
      }
    },
    mounted () {
      this.imFile = document.getElementById('imFile')
      this.outFile = document.getElementById('downlink')
    },
    methods: {
      showTrain (convertedData) {
        console.log(convertedData)
      },
      run () {
        const postCfg = {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }
        var params = {
          'trainingData': this.trainingData,
          'testData': this.testData
        }
        console.log(params)
        this.$http.post('/alg/run', JSON.stringify(params), postCfg).then(res => {
          this.result = res.body
          console.log(this.result)
          for (let i = 0; i < this.result.length; i++) {
            this.testData[i]['result'] = this.result[i]
          }
          this.resultShow = true
        })
      },
      uploadFile: function (res) { // 
        this.butt = res
        this.imFile.click()
      },
      downloadFile: function (rs) { // 
        let data = [{}]
        for (let k in rs[0]) {
          data[0][k] = k
        }
        data = data.concat(rs)
        this.downloadExl(data, '')
      },
      importFile () { // excel
        this.fullscreenLoading = true
        let obj = this.imFile
        if (!obj.files) {
          this.fullscreenLoading = false
          return
        }
        var f = obj.files[0]
        var reader = new FileReader()
        let $t = this
        reader.onload = function (e) {
          var data = e.target.result
          if ($t.rABS) {
            $t.wb = XLSX.read(btoa(this.fixdata(data)), {  // 
              type: 'base64'
            })
          } else {
            $t.wb = XLSX.read(data, {
              type: 'binary'
            })
          }
          let json = XLSX.utils.sheet_to_json($t.wb.Sheets[$t.wb.SheetNames[0]])
          $t.dealFile($t.analyzeData(json)) // analyzeData: 
        }
        if (this.rABS) {
          reader.readAsArrayBuffer(f)
        } else {
          reader.readAsBinaryString(f)
        }
      },
      downloadExl: function (json, downName, type) {  // excel
        let keyMap = [] // 
        for (let k in json[0]) {
          keyMap.push(k)
        }
        console.info('keyMap', keyMap, json)
        let tmpdata = [] // json
        json.map((v, i) => keyMap.map((k, j) => Object.assign({}, {
          v: v[k],
          position: (j > 25 ? this.getCharCol(j) : String.fromCharCode(65 + j)) + (i + 1)
        }))).reduce((prev, next) => prev.concat(next)).forEach(function (v) {
          tmpdata[v.position] = {
            v: v.v
          }
        })
        let outputPos = Object.keys(tmpdata)  // ,A1D10
        let tmpWB = {
          SheetNames: ['mySheet'], // 
          Sheets: {
            'mySheet': Object.assign({},
              tmpdata, // 
              {
                '!ref': outputPos[0] + ':' + outputPos[outputPos.length - 1] // 
              })
          }
        }
        let tmpDown = new Blob([this.s2ab(XLSX.write(tmpWB,
          {bookType: (type === undefined ? 'xlsx' : type), bookSST: false, type: 'binary'} // 
        ))], {
          type: ''
        })  // 
        var href = URL.createObjectURL(tmpDown)  // 
        this.outFile.download = downName + '.xlsx'  // 
        this.outFile.href = href  // a
        this.outFile.click()  // 
        setTimeout(function () {  // 
          URL.revokeObjectURL(tmpDown) // URL.revokeObjectURL()object URL
        }, 100)
      },
      analyzeData: function (data) {  // 
        return data
      },
      dealFile: function (data) {   // 
        this.imFile.value = ''
        this.fullscreenLoading = false
        if (data.length <= 0) {
          this.errorDialog = true
          this.errorMsg = ''
        } else {
          if (this.butt === 'train') {
            this.trainingData = data
          } else if (this.butt === 'test') {
            this.testData = data
          } else if (this.butt === 'jpg') {
            this.loggingImage = data
          }
          console.log(data)
        }
      },
      s2ab: function (s) { // 
        var buf = new ArrayBuffer(s.length)
        var view = new Uint8Array(buf)
        for (var i = 0; i !== s.length; ++i) {
          view[i] = s.charCodeAt(i) & 0xFF
        }
        return buf
      },
      getCharCol: function (n) { // 26[0-25] -> [A-Z]
        let s = ''
        let m = 0
        while (n > 0) {
          m = n % 26 + 1
          s = String.fromCharCode(m + 64) + s
          n = (n - m) / 26
        }
        return s
      },
      fixdata: function (data) {  // BinaryString
        var o = ''
        var l = 0
        var w = 10240
        for (; l < data.byteLength / w; ++l) {
          o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)))
        }
        o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)))
        return o
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .el-table th>.cell {
    text-align: center;
  }
  .button {
    margin-bottom: 20px;
  }
   .text {
     font-size: 14px;
   }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }

</style>



